  width: fit-content;
  padding: 4px 3px;
  position: relative;
  margin: 4px 0;
}
.class_new::after {
  position: absolute;
    border: 1px solid #222;
    border-radius: 4px;
    content: 'new';
    right: -15px;
    top: -6px;
    font-size: 10px;
    font-weight: 900;
    background: #fff;
    line-height: 12px;
    padding: 0 5px;
}
</style>
